import Vue from 'vue'
import Router from 'vue-router'
import Config from '@/pages/config'
import HomePage from '@/pages/homePage'
import suc from '@/pages/sucessPage'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index',
      name: 'homePage',
      component: HomePage
    },
    {
      path: '/',
      name: 'config',
      component: Config
    },
    {
      path: '/suc',
      name: 'suc',
      component: suc
    }
  ]
})



